import type { Dayjs } from 'dayjs'
import dayjs from 'dayjs'
import { useSnackbar } from 'notistack'
import type { ChangeEvent } from 'react'
import { useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router-dom'

import { Box, Card } from '@mui/material'

import { type ClubMember } from 'src/models/ClubMember'
import type { ClubMemberForAuth } from 'src/models/ClubMember'
import { type DateProps } from 'src/models/date'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import ConferenceInfo from 'src/content/manage/Conference/ConferenceEdit/ConferenceInfo'

import { useCreateConference } from 'src/hooks/api/clubConferenceHooks'

const ConferenceRegister: React.FC = () => {
  const [title, setTitle] = useState<string>('')
  const [date, setDate] = useState<Dayjs | null>(dayjs())
  const [selectedMembers, setSelectedMembers] = useState<number[]>([])

  const params = useParams()
  const { enqueueSnackbar } = useSnackbar()

  const changeTitle = (title: string) => {
    setTitle(title)
  }

  const authUser = useAuthUser()
  const clubId = Number(params.id)
  const club = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId)[0] as ClubMemberForAuth

  const clubDate: DateProps = { year: club.year, semester: club.semester }

  const createConferenceMutation = useCreateConference({
    clubId,
    title: title,
    startAt: date,
    users: selectedMembers,
    clubDate: clubDate
  })

  const changeDate = (date: Dayjs | null) => {
    setDate(date)
  }

  const selectAllMembers = (event: ChangeEvent<HTMLInputElement>, members: ClubMember[]) => {
    setSelectedMembers(event.target.checked ? members && members.map(member => member.id) : [])
  }

  const selectOneMember = (memberId: number) => {
    setSelectedMembers(prevSelected => [...prevSelected, memberId])
  }

  const deleteOneMember = (memberId: number) => {
    setSelectedMembers(prevSelected => prevSelected.filter(id => id !== memberId))
  }

  const selectAllLevelMember = (levelMembers: number[]) => {
    setSelectedMembers(prevSelected => Array.from(new Set([...prevSelected, ...levelMembers])))
  }

  const deleteAllLevelMember = (levelMembers: number[]) => {
    setSelectedMembers(prevSelected => prevSelected.filter(memberId => !levelMembers.includes(memberId)))
  }

  const handleSubmit = async () => {
    if (title.length > 50) {
      enqueueSnackbar('행사명은 50자 이내로 작성해 주십시오.', { variant: 'error' })
      return
    }
    createConferenceMutation.mutateAsync()
  }

  return (
    <Box m={3}>
      <Helmet>
        <title>출석 관리</title>
      </Helmet>
      <Card>
        <ConferenceInfo
          title={title}
          changeTitle={changeTitle}
          date={date}
          changeDate={changeDate}
          selectedMembers={selectedMembers}
          selectAllMembers={selectAllMembers}
          selectOneMember={selectOneMember}
          deleteOneMember={deleteOneMember}
          selectAllLevelMember={selectAllLevelMember}
          deleteAllLevelMember={deleteAllLevelMember}
        />
        <CancelSubmitBtns handleSubmit={handleSubmit} />
      </Card>
    </Box>
  )
}

export default ConferenceRegister
